.dashboard-container {
  padding: 2rem;
  font-family: Arial, sans-serif;
}

.dashboard-title {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.summary-cards {
  display: flex;
  gap: 2rem;
  font-size: 1.1rem;
  margin-bottom: 2rem;
}

.chart-section {
  margin-top: 2rem;
}

.transactions-section {
  margin-top: 2rem;
}

.transactions-table {
  width: 100%;
  border-collapse: collapse;
}

.transactions-table th,
.transactions-table td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: left;
}

.transactions-table thead {
  background-color: #f4f4f4;
}

.loading {
  padding: 2rem;
  font-size: 1.2rem;
}
